
import '../Infra/global.script'
import Menu from '../Components/Menu'
import MyPage from '../Pages/Dashboard'

class AppRouter  {

    constructor(url) { this.url = url; }

    getProperView(newState, params) {        
        return new MyPage(params)        
    }

    getHtml() {        
        var newState = this.url        
        var params = { id: null };
        var route_values = newState.split("/");
        newState = '/' + route_values[1]
        if (route_values.length >= 2)
            params.id = route_values[2]
        return `<div id='splash' align='center' style='display:none;width:100%;height:100%;'></div>
                <div class="wrapper" align="center">${Menu.getHtml()}<div class="wrapper-inline img shadow" >
                    <header id='appHeader'>
                        <img src='src/static/img/topLogo.jpg' alt='iFix' style='width:60px;height:32px;'/>
                        <div class="navi-menu-button"><em></em><em></em><em></em></div>
                    </header>
                    <main><section class="container"><br>${this.getProperView(newState, params).getHtml()}</section></main>
                </div></div>`;
    }
}

export default class {
    constructor(params) {
        this.params = params;
    }
    getHtml() { return new AppRouter(this.params).getHtml(); }
}

const navigateTo = url => {
    history.pushState(null, null, url);
    router();
};

const router = () => {
    const view = new AppRouter(window.location.pathname);
    document.querySelector("#myApp").innerHTML = view.getHtml();
};

window.addEventListener("popstate", router);

document.addEventListener("DOMContentLoaded", () => {
    document.body.addEventListener("click", e => {
        if (e.target.matches("[data-link]")) {
            e.preventDefault();
            navigateTo(e.target.href);
        }
    });
    router();
});
