
import { MultiLanguage } from '../Infra/Language'

export default class {

	static getHtml() {
		var dashboardItem = MultiLanguage(12)
		var userItem = MultiLanguage(13)
		var loginItem = MultiLanguage(14)
		var registerItem = MultiLanguage(15)

		return `<div class="nav-menu" align='left'><nav class="menu">
				<div class="nav-header" style='height:150px'><a href='/' style='cursor:pointer'><img src="src/static/img/menuLogo.png" alt='Dashboard' style="width:300px;height:105px;padding-top:15px"></a></div>
				<div class="nav-container">
					<ul class="main-menu">
						<li class="active"><a href='/' style='cursor:pointer'><i class="fa fa-home"></i> ${dashboardItem}</a></li>
						<li>
							<a href="javascript:void(0);"><i class="fa fa-sign-in"></i> ${userItem} <span class="fa fa-angle-down"></span></a>
							<ul>
								<li><a href="/login" style='cursor:pointer'>${loginItem}</a></li>
								<li><a href="/register" >${registerItem}</a></li>									
							</ul>
						</li>
					</ul>
				</div>
			</nav>
		</div>`;
	}
}
