
import { 
    getFromSession, 
    setToSession,
    fadeIn,
    fadeOut,
} from '../Infra/Util'

import Splash from '../Components/Forms/Splash'

export default class {

    constructor(params) {
        this.params = params;
        $(document).ready(function () {
            if (getFromSession('splashed') !== 'true' ) {
                setToSession('splashed', 'true')
                $("body").css('background-color', '#FFFFFF');
                $("#splash").html (Splash.getHtml())
                $("#appHeader").css('display', 'none');
                setTimeout(function(){
                    fadeIn ($("#splash")[0], 400);                    
                    setTimeout(function(){
                        fadeOut ($("#splash")[0],400);
                        setTimeout(function(){
                            $("#splash").css('display', 'none');
                            $("body").css('background-color', '#CFCFCF');
                            $("#appHeader").css('display', 'block');
                            $("#appHeader").fadeIn();
                        },400);                  
                    },2000);                    
                },900);
            }
        });
    }

    getHtml() { return ""; }
}
